// ** React Imports
import { Fragment, useEffect, useState } from 'react'

// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Form,
  Badge,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  Table,
  Modal,
  ModalBody,
  ModalHeader,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'
import Select from 'react-select'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { getInvoiceAccount, addAccount, updateAccount, deleteAccount, updatePrimary } from './store/invoiceaccount'
import { activeOrganizationid, orgUserId, activeOrganization } from '@src/helper/sassHelper'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const activeOrg = activeOrganization()
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components
import classnames from 'classnames'
import { useDispatch, useSelector } from 'react-redux'
import toast from 'react-hot-toast'
import { CheckCircle, Edit, MoreVertical, Plus, Trash } from 'react-feather'

const InvoiceAccounts = () => {
  const MySwal = withReactContent(Swal)
  const [data, setData] = useState([])
  const [selected, setSelected] = useState(null)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const [formModal, setFormModal] = useState(false)
  const store = useSelector(state => state.invoiceaccount)
  const dispatch = useDispatch()

  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    bankName: yup.string().required('Please Enter Bank Name'),
    accountBusinessName: yup.string().required('Please Enter Account Name'),
    countryCode: yup.string().default(activeOrg.countryshortname),
    ifscCode: yup.string().default('').when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please IFSC Code') }),
    accountNumber: yup.string().required('Please Enter Account Number'),
    branchAddress: yup.string().default('').when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Enter Branch Name') }),
    accountType: yup.string().required('Please Select Account Type'),
    description: yup.string(),
    isPrimary: yup.boolean().default(false),
    bankCode: yup.string().default('1'),
    accountHolderName: yup.string(),
    updatedBy: yup.string().default(userId),
    createdBy: yup.string().default(userId)
  })

  const { handleSubmit, formState: { errors }, control, reset, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteAccount(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Account has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        await dispatch(getInvoiceAccount({}))
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const closeModal = () => {
    setFormModal(false)
    reset({
      organizationId: activeOrgId,
      bankName: '',
      id: null,
      accountBusinessName: '',
      ifscCode: '',
      accountNumber: '',
      branchAddress: '',
      accountType: 1,
      description: '',
      isPrimary: '',
      bankCode: '',
      accountHolderName: ''
    })
  }

  const onSubmit = async data => {
    setBlock(true)
    let eventname = ''
    if (selected !== null) {
      await dispatch(updateAccount(data))
      setSelected(null)
      eventname = 'Added'
    } else {
      await dispatch(addAccount(data))
      eventname = 'Updated'
    }
    setBlock(false)
    closeModal()
    toast.success(`Bank Account ${eventname} Succesfully`, { position: 'bottom-right' })
    await dispatch(getInvoiceAccount({}))

  }

  useEffect(async () => {
    await dispatch(getInvoiceAccount({}))
  }, [])

  useEffect(() => {
    const primaryAcc = store.accounts.find((acc) => acc.isPrimary) ?? {}
    setValue('isPrimary', Object.keys(primaryAcc).length === 0)
    setData(store.accounts)
  }, [store.accounts])

  const formatAccountName = () => {
    const name = control._formValues.bankName
    const num = control._formValues.accountNumber
    if (num) {
      const temp = `${name} - XXXX${num.substr(-4, 4)}`
      setValue('accountHolderName', temp)
    }

  }

  const accountTypeOptions = [{ id: 1, name: "Savings" }, { id: 2, name: `${activeOrg.countryshortname === 'IN' ? 'Current' : 'Checking'}` }]

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={6}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field}
              onInput={() => { if (fieldName === 'bankName' || fieldName === 'accountNumber') { formatAccountName() } }} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const getSelectRow = (fieldLabel, fieldName, options, reqflag = false) => {
    return (

      <Col md={6}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName} >
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            control={control}
            name={fieldName}
            id={fieldName}
            render={({ field, ref }) => (
              <Select
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors[fieldName] })}
                {...field}
                classNamePrefix='select'
                options={options}
                value={options.find(c => { return c.id === field.value })}
                onChange={val => { return field.onChange(val.id) }}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
              />
            )}

          />
          {errors[fieldName] && <FormFeedback className='text-danger'>{errors[fieldName]?.message}</FormFeedback>}
        </Col>
      </Col>

    )
  }

  const openEditModal = card => {
    setSelected(card)
    setFormModal(true)
    reset({
      organizationId: activeOrgId,
      bankName: card.bankName,
      id: card.id,
      accountBusinessName: card.accountBusinessName,
      ifscCode: card.ifscCode,
      accountNumber: card.accountNumber,
      branchAddress: card.branchAddress,
      accountType: parseInt(card.accountType),
      description: card.description,
      isPrimary: card.isPrimary,
      bankCode: card.bankCode,
      accountHolderName: card.accountHolderName
    })
  }

  const updateprimary = (id) => {
    dispatch(updatePrimary(id))
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody className='py-25'>
            <Row className='gx-4'>
              <Col lg='12' className='mt-2 mt-lg-0'>
                <Row className='mb-50'>
                  <Col>
                    <Button color='primary' size='sm' onClick={() => { setFormModal(true) }}> <Plus size={15} />Add Account</Button>
                  </Col>
                </Row>
                <Table className='table table-hover table-bordered'>
                  <thead>
                    <tr>
                      <th>Bank Name</th>
                      <th>Account Holder Name</th>
                      <th>Status</th>
                      <th>Account Name </th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Array.isArray(data) && data.map((card, index) => {
                      return (
                        <tr
                          key={index}
                        >
                          <td className='text-primary'>{card.bankName}</td>
                          <td>
                            {card.accountBusinessName}</td>
                          <td>
                            {card.isPrimary && (
                              <Badge color='light-primary' className='ms-50'>
                                Primary
                              </Badge>
                            )}
                          </td>
                          <td className='card-number '>
                            {card.accountHolderName}
                          </td>
                          <td className='text-start text-lg-end'>
                            <div className='d-flex align-items-center order-sm-0 order-1 mt-1 mt-sm-0'>
                              {card.accountHolderName !== 'Cash' &&
                                <div className='d-flex order-sm-0 order-1 mt-1 mt-sm-0'>
                                  <Edit onClick={() => openEditModal(card)} size={14} />
                                  {!card.isPrimary &&
                                    <div className='d-flex'>
                                      <Trash className='ms-1 me-1' onClick={() => { deletefn(card.id) }} size={14}></Trash>
                                    </div>
                                  }
                                </div>
                              }
                              {!card.isPrimary &&
                                <UncontrolledDropdown>
                                  <DropdownToggle tag='div' className='btn btn-sm p-0'>
                                    <MoreVertical size={14} className='cursor-pointer' />
                                  </DropdownToggle>
                                  <DropdownMenu>
                                    <DropdownItem className='w-100' onClick={() => { updateprimary(card.id) }}>
                                      <CheckCircle size={14} className='me-50' />
                                      <span className='align-middle'>Mark as Primary</span>
                                    </DropdownItem>
                                  </DropdownMenu>
                                </UncontrolledDropdown>
                              }
                            </div>
                          </td>
                        </tr>
                      )
                    })}
                  </tbody>
                </Table>
              </Col>

              <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
                <ModalHeader toggle={() => closeModal()}>Invoice Account</ModalHeader>
                <ModalBody>
                  <Col lg='12'>
                    {
                      Object.keys(errors).map((key) => {

                        return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

                      })
                    }
                    <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

                      {getRow('Bank Name', 'bankName')}
                      {getRow('Account Name', 'accountBusinessName')}
                      {getRow('Account Number', 'accountNumber')}

                      {activeOrg.countryshortname === 'IN' &&
                        getRow('IFSC Code', 'ifscCode')
                      }
                      {activeOrg.countryshortname === 'IN' ? getRow('Branch Name', 'branchAddress') : getRow('Branch Name', 'branchAddress', false)}

                      {getSelectRow('Account Type', 'accountType', accountTypeOptions, true)}

                      <Col md={12}>
                        <Label className='form-label' for='description'>
                          Description
                        </Label>
                        <Controller
                          id='description'
                          name='description'
                          control={control}
                          render={({ field }) => <Input type="textarea" invalid={errors.description && true} {...field} />}
                        />
                        {errors.description && <FormFeedback>{errors.description.message}</FormFeedback>}

                      </Col>
                      <Col className='mt-2 pt-1' xs={12}>
                        <Button type='submit' className='me-1' color='primary'>
                          Submit
                        </Button>
                        <Button type='button' className='me-1' color='warning' onClick={() => closeModal()}>
                          Cancel
                        </Button>
                      </Col>

                    </Row>
                  </Col>
                </ModalBody>
              </Modal>
            </Row>
          </CardBody>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default InvoiceAccounts
